<template>
	<view class="">
		<view class="" style="position: relative;" >
			<!-- <scroll-view scroll-y="true" style="height: calc(100vh - 135rpx);"> -->
				<view class="" style="height: 25rpx;"></view>
					<view class="" v-if="dates.backgroundUrl" style="margin: 0 126rpx; width: 498rpx; height: 280rpx;">
						<image class="" :src="dates.backgroundUrl"  
							style="width: 498rpx; height: 280rpx;  border-radius: 22rpx;"></image>
					</view>
					<view class="ta-c fw800 bgc-o-y" style="">
						<view class="" style="height: 5rpx;"></view>
						<view class="f34rpx color-Oeb5d2d" style="line-height: 36rpx;">{{dates.title}}</view>
						<view class="f18rpx color-H364d5a">Date Published:</view>
						<view class="f18rpx color-H364d5a ">{{day}}</view> 
						<view class="" style="height: 13rpx;"></view>
					</view>
					<view class="" v-if="dates.description" style="width: 100%; background-color: #fefefe; height: calc(100vh - 502rpx);">
						<view class="" style="height: 25rpx;"></view>
						<view class="color-H364d5a f22rpx fw800" style="line-height: 25rpx; margin: 0 86rpx 0 55rpx;">
							{{dates.description}}    
						</view>
						<view class="" style="height: 48rpx;"></view> 
						<view class="fw800 color-H364d5a f32rpx ta-c" style="">GALLERY</view>
						<view class="" style="height: 16rpx;"></view>
						<view  class="" style="margin: 0 49rpx; height: 368rpx;">
							<swiper style="height: 100%; width: 100%;" 
							@change="swiperFun">
								<swiper-item>
										<view  style="position: relative;" class="" v-show="ifVideo">
											<image :src="dates.videoImageUrl" mode="widthFix" 
												style="width: 100%;"></image>
											<view class="" 
												style="position: absolute; top: 144rpx; left: 286rpx;">
												<image @click="clickImage" src="../../static/Index/PlayButtoncopy3.png" 
													mode="widthFix" style="width: 100rpx;"></image> 
											</view>
										</view> 
										<video v-if="!ifVideo"
											id="myVideo"
											@pause="videoStop"
											@ended="videoStop"
											:show-center-play-btn="false"
											:autoplay="true"
											:show-fullscreen-btn="true"
											object-fit="fill"
											style="width: 100%; height: 100%; z-index: 2;" 
											:src="dates.videoUrl"> 
										</video>	
								</swiper-item>
								<swiper-item>
									<image :src="dates.screenshotList[0]" mode="widthFix" 
									style="width: 100%;"></image>
								</swiper-item>
								<swiper-item>
									<image :src="dates.screenshotList[1]" mode="widthFix" 
									style="width: 100%;"></image>
								</swiper-item>
								<swiper-item>
									<image :src="dates.screenshotList[2]" mode="widthFix" 
									style="width: 100%;"></image>
								</swiper-item>
							</swiper>
							<view class="df-jcc-aic"  style="">
								<view class="" v-for="(item,index) in h" :key="index">
									<view class="ss" style="" :class="{active:index == num }"></view> 
								</view>
							</view>
						</view>
				</view>
				<view v-if="dates.isNew" class="bgc_FFF" style="height: 20rpx;"></view>
			<!-- </scroll-view> -->
		</view>
	</view>
</template>

<script>
	var timer = null
	export default {
		props:{
			dates:Object,
			day:String
		},
		data() {
			return {
				num:0,
				h:4,
				ifVideo:true,
				ifReady:false,
				// videoContext:{},
				dblClick:false, //判断是否点击
				pageX:0,
				pageXEnd:0,
			}
		}, 
		mounted() {
			// console.log('mounted');
			this.videoContext=uni.createVideoContext('myVideo',this)
			// console.log(this.videoContext);
		},
		methods: {
			sss(){
				console.log('sss');
			},
			touchstart(res){
				// console.log(res);
				this.pageX = res.changedTouches[0].pageY
				this.videoContext.pause()
				console.log('this.pageX');
			},  
			touchend(res){
				// console.log(res);
				console.log(this.pageXEnd);
				this.pageXEnd = res.changedTouches[0].pageY
				if(this.pageXEnd - this.pageX >=0){
					this.videoContext.pause()
					console.log('触摸了开始了');
				}
				console.log('触摸了结束了');
			},  
			player(){
				//从头播放视频
				if(this.play===false){
					this.videoContext.seek(0)
					this.videoContext.play()
					this.play=true
				}
			},
			pause(){
				//暂停视频
				this.videoContext.pause()
			},
			playThis(){
				//播放当前视频
				this.videoContext.play()
			},
			clickImage(){
				this.videoContext.play()
				console.log('点击开始播放');
				this.ifVideo = false
			},
			videoStop(){
				// console.log('暂停了');
				this.ifVideo = true
			},
			swiperFun(e){
				this.num = e.detail.current
			},
			saoYiSao(){
				uni.scanCode({
					 
				})
			},
			fanHui(){
				this.videoContext.pause()
				this.$emit("forGameDetail")
			},
		},
		beforeDestroy(){
			console.log('组件即将销毁');
			this.videoContext.pause()
		}
		 
	}
</script>

<style scoped>

	.Teky1{
		position: absolute; 
		top: 29rpx; 
		width: 60rpx; 
		height: 60rpx;
	}
	.bgc{
		background-color: #eb5d2d;
	}
	.border-sj-left{
		width: 0;
		height: 0;
		border-top: 40rpx solid #eb5d2d; 
		border-left: 50rpx solid transparent; 
	}
	.border-sj-right{
		width: 0;
		height: 0;
		border-top: 40rpx solid #eb5d2d;
		border-right: 50rpx solid transparent; 
	}
	.ss{
		width: 20rpx; 
		height: 20rpx; 
		border-radius: 50%; 
		background-color: #ee5b30; 
		margin: 10rpx;
		 
	}
	.active{
		width: 30rpx;
		height: 30rpx; 
		border-radius: 50%; 
		background-color: #ee5b30; 
	}
</style>
