<template>
<view class="">
	<view class="" style="">
		<!-- <view class="" style="padding-top: 450rpx;"></view> -->
		<view class="" v-show="GFlag"> 
			<view class="FFF f36rpx ta-c fw800" style="">{{title}} GAMES</view>
			<view class="" style="height: 0rpx;"></view>
			<view class="" style="z-index: 2; position: relative;" >
				<view @click="fanHuiGCenter" class="" style="position: absolute; top: -41rpx; left: 60rpx;"
					hover-class="hover1" hover-start-time=0 hover-stay-time=150 >
					<image src="../../static/Index/fanHui.png" style="width: 22rpx;" mode="widthFix"></image>
				</view>
				<view @click="goSearch" class="" style="position: absolute; top: -41rpx; right: 60rpx;"
					hover-class="hover1" hover-start-time=0 hover-stay-time=150 >
					<image src="../../static/games_center/SearchIconRight.png" style="width: 40rpx;" 
					mode="widthFix"></image>
				</view>  
			</view> 
				<!-- 返回，搜索 -->
			<view class="" style="height: 69rpx;"></view>
			<view class="df-jcsa" style="position: relative; ">
				<view class="" style="width: 75rpx; height: 515rpx;">
					<view class="" style="height: 401rpx;"></view>
					<view class="" :style="bgc" style="height: 115rpx; border-top-left-radius: 40rpx;"></view>
				</view>
				<view class="top-left-right" :style="bgc" style="width: calc(100vw - 150rpx); height: 515rpx;">
					<view class="b-r-30 bgc_FFF" style="width: calc(100vw - 150rpx); height: 515rpx;">
						<view class="df bb-r-lr30" :style="bgc" style="height: 161rpx;"> 
							<view class="df-jcc-aic" style="padding-left: 34rpx;">
								<image :src="img" class="b-r-50" mode="widthFix" style="width: 120rpx;"></image>
							</view> 
							<view class="FFF fw800 f45rpx" 
							style="padding-left: 27rpx; letter-spacing:5rpx; height: 100%; line-height: 161rpx;">
							{{title}}</view>  
						</view> 
						<view class=""> 
							<view class="" style="">
								<view class="" style="height: 23rpx;"></view>
								<view class="df-jcc" style="" v-if="videoFlag" @touchstart.stop="touchstart">
									<video 
									id="myVideo1" 
									:loop="false"
									:autoplay="true"
									:enable-progress-gesture="false"
									:controls="true"
									:show-center-play-btn="false"
									object-fit="fill"
									:src="TramListData.videoUrl" 
									@pause="videoPause"
									@ended="videoPause"
									style="height: 308rpx; width: 550rpx;" >
									</video>
								</view> 
								<view class="df-jcc" v-show="!videoFlag">
									<view  style="position: relative; height: 308rpx; width: 550rpx;" class="">
										<image src="../../static/Index/ScreenShot.png" mode="" 
											style="height: 308rpx; width: 550rpx;"></image>
										<view class="" 
											style="position: absolute; top: 114rpx; left: 235rpx;">
											<image @click="clickImage" src="../../static/Index/PlayButtoncopy3.png" 
												mode="widthFix" style="width: 80rpx;"></image> 
										</view>
									</view>
								</view>
								<view class="" style="height: 23rpx; width: 100%;"></view>
							</view>
						</view>
					</view>
				</view>
				<view class="" style="width: 76rpx; height: 515rpx;">
					<view class="" style="height: 401rpx;"></view>
					<view class="" :style="bgc" style="height: 115rpx; border-top-right-radius: 40rpx;"></view>
				</view>
			</view> 
			
			<view class="top-left-right" :style="bgc" >
				<view class="bgc_L2c9  " :style="bgc">
					<view class="" style="height: 12rpx;"></view>
					<view class="df-jcsb" style="margin: 0 74rpx; flex-wrap: wrap;">
						<view class="" v-for="(item,index) in (TramList)" :key="item.id">
							<view @click="goAdventure(item.id,item.icon_url,item.name)" class="br22rpx games1"
								hover-class="hours_174" hover-start-time=0 hover-stay-time=150
								 :style='box_sha' style="margin-bottom: 15rpx;">
								<view class="" style="height: 30rpx;"></view> 
								<view class="df-jcc-aic" style="height: 150rpx;">
									<view class="df-jcc-aic" style="height: 145rpx;">
										<image :src="item.icon_url"  
											mode="heightFix" style="height: 145rpx;"></image>
									</view> 
								</view>
								<view class="FFF f28rpx ta-c" style="line-height: 49rpx;">
									{{item.name}}
								</view>
							</view>
						</view>
					</view> 
				</view> 
				<view class="" style="height: calc(100vh - 1290rpx);"></view>
			</view>	
			<!-- <view class="" style="height: 50rpx;"></view> -->

		</view>
	</view>
	<view class="" v-if="searchFlag">
		<!-- <Search @searchBack="searchBack"></Search> -->
		<view class="bgc-o-r" style="height: calc(100vh - 193rpx);">
			<!-- <view class="" style="height: 32rpx;"></view> -->
			<view class="df-jcsb" style="margin: 0 75rpx;"> 
				<view @click="searchGoBack" class="df-jcc-aic"
				hover-class="hover1" hover-start-time=0 hover-stay-time=150 >
					<image src="../../static/Index/fanHui.png" mode="widthFix" style="width: 22rpx;"></image>
				</view>
				<view class="bgc_FFF df-jcsb" 
				style="height: 82rpx; width: 502rpx; border-radius: 41rpx; margin:0 20rpx 0 26rpx ;">
					<view class="" style="padding-left: 50rpx; height: 100%;">
						<input style=" height: 100%; color: #ee5b30;" 
						v-model="searchData" type="text" value="" />
					</view> 
					<view @click="clearData" class="df-jcc-aic" style="padding-right: 15rpx;"
					hover-class="hover1" hover-start-time=0 hover-stay-time=150 >
						<image src="../../static/games_center/XShape.png" mode="widthFix" style="width: 52rpx;"></image>
					</view>
				</view>
				<view class="df-jcc-aic" @click="searchDate"
				hover-class="hover1" hover-start-time=0 hover-stay-time=150 >
					<image src="../../static/games_center/SearchIconRight.png" 
					mode="widthFix" style="width: 40rpx;"></image>
				</view>
			</view>
			<view class="" style="height: 20rpx;"></view>
			<view class="" style="margin: 0 44rpx;">		
			<scroll-view scroll-y="true" class="bgc_FFF b-r-l-r-t46" style="height: calc(100vh - 295rpx); ">
				<view v-show="souSuoQ" class="" style="padding: 31rpx 33rpx  0;">
					<view class="color-Oeb5d2d fw800 f27rpx">
						By Popular
					</view>
					<!-- 搜索记录 -->
					<view v-if="ifhistory" class="color-Oeb5d2d fw800 f24rpx" style="margin: 40rpx 0 0 40rpx;">
						<view class="df-jcsb">
							<view class="">
								Top5
							</view>
							<view class="">
								<image src="../../static/games_center/BackArrowcopy4.png" mode="widthFix" 
								style="width: 30rpx;"></image>
							</view>
						</view>
						<view class="">
							<view class="df-jcsb" v-for="(item,index) in seaDate" :key="index" 
								style="margin-top: 40rpx;">{{item}}
								<view class="" @click="menubtn(item)">
									<image src="../../static/games_center/BackArrowcopy4.png" mode="widthFix" 
									style="width: 30rpx;"></image>
								</view>
							</view>
						</view>
					</view>
				</view>
				
				<view class="" v-show="!souSuoQ" style="padding: 31rpx 33rpx  0;">
					<view class="color-Oeb5d2d fw800 f27rpx">
						Results...
					</view>
					<view class="" style="height: 20rpx;"></view>
					<view class="">
						<view class="df-jcsb b-r-30 bgc_fa" @click="fogetSeaDate(i)"
							v-for="(i,index1) in getSeaDate" 
							:key="index1"
							hover-class="hours_174" hover-start-time=0 hover-stay-time=150
							style="height: 150rpx; margin: 10rpx 0rpx;">
							<view class="df-jcc-aic" style="margin-left: 18rpx;">
								<image :src="i.iconUrl" mode="widthFix" style="width: 120rpx;"></image>
							</view>
							<view class="df-jcc-aic">
								<view class="f24rpx fw800" style="color: #ee5b30;">{{i.title}}</view>
							</view>
							<view class="df-jcc-aic" style="margin-right: 23rpx;">
								<view class="b-r-50 df-jcc-aic" 
								style="width: 79rpx; height: 79rpx; border: 2rpx solid #cfcece; box-shadow: 0rpx 0rpx 3rpx #cfcece;">
									<image src="../../static/img/Polygon1715.png" mode="widthFix" style="width: 29rpx;"></image>
								</view>
							</view>
						</view>
					</view>
					<view class="" style="height: 20rpx;"></view>
				</view>
			</scroll-view>
			</view>
		</view>
	</view> 
	<!--  -->
	<view class="" v-show="AdventureFlag"> 
		<gamesData @slidingBack="slidingBack" :bgcH="ifx == 'ifL' ? bgcL : bgcZ" :gameData="gameData"
		:smallImg="ifx == 'ifL' ? smallImgL : smallImgZ" :src="url" :titleName="title"	:title="name"></gamesData>
	</view>
	<view @click="backGameDetail" v-if="gameDetailFlag"   class="" 
		hover-class="hover1" hover-start-time=0 hover-stay-time=150
		style="position: absolute; left: 50rpx; top: 200rpx; z-index: 3;">
		<image src="../../static/Index/fanHui.png" mode="widthFix" style="width: 25rpx;"></image>
	</view> 
	<view class="" v-show="gameDetailFlag">
		<gameDetail :dates="dates" :day="days"></gameDetail>
	</view>

	
	
</view>  
</template>

<script>
	import gameDetail from 'pages/index/gameDetails.vue'
	import gamesData from './gamesData.vue'
	export default {
		components:{gamesData,gameDetail},   
		props:{
			ifx:String,
			bgc:String,
			box_sha:String,
			Flag:Boolean,
			title:String,
			img:String,
			TramData:Object,
			TramListData:Object,
			TramList:Array,
			ifReday:Boolean
		},
		data(){
			return{
				isSwitch:'gamesData', 
				GFlag:true,
				searchFlag:false, 
				AdventureFlag:false,
				CompetitionFlag:false,
				JourneyFlag:false,
				SongDanceFlag:false,
				titleFlag:true,
				videoFlag:false,
				souSuoQ:true,
				gameDetailFlag:false,
				str:'Adventure' ,
				srcA:'../../static/games_center/Wave-big.png',
				srcC:'../../static/games_center/Ellipse6.png',
				srcJ:'../../static/games_center/feet1.png',
				srcS:'../../static/games_center/Line7.png',
				bgcL:"background-color: #2b94d0;box-shadow: 2rpx 2rpx 5rpx #44c0dd;border: 1rpx solid #44c0dd;",
				bgcZ:"background-color: #a0599f;box-shadow: 2rpx 2rpx 5rpx #a38461;border: 1rpx solid #d38bd2;",
				smallImgL:'../../static/games_center/sanjiaoxing.png',
				smallImgZ:'../../static/games_center/Polygon1715.png',
				
				gameData:{},
				url:'',
				name:'',
				//搜索
				searchData:'', //输入的搜索历史
				ifhistory:false,
				seaDate:[],
				getSeaDate:{},
				dates:{},
				days:'',
				day:''
			}
		},
		
		mounted() {
			this.videoContext1 = uni.createVideoContext('myVideo1');//创建视频实例指向video
			// console.log(this.videoContext);
		},
		updated() {
			// this.setStorages()
		}, 
		methods:{
			fogetSeaDate(e){
				// console.log(e);
				this.searchFlag = false
				this.gameDetailFlag = true
				this.dates = e
				this.day = e.createTime
				// console.log('day='+this.day);
				let year = this.day.slice(0,4)
				let month = this.day.slice(5,7)
				let day = this.day.slice(8,10)
				this.days = day + '/'+month+'/'+year
			},
			backGameDetail(){
				// console.log('返回');
				this.searchFlag = true
				this.gameDetailFlag = false
			},
			//搜索
			async searchDate(){
				this.souSuoQ = false
				const res = await this.$myRequest({
					url:'/api/game/list'+'?gameName='+this.searchData+'&page=1'+'&limit=-1'
				})
				// console.log('成功1');
				this.getSeaDate = res.data.data.records
				// console.log(this.getSeaDate.data);
				if(this.searchData != ''){
					this.getSearchHistory(this.searchData)
				}
			},
			// 清除输入框内容
			clearData(){ 
				this.souSuoQ = true
				this.searchData = ''
				this.getSeaDate = ''
			},
			// 返回上一层
			searchGoBack(){ 
				// this.souSuoQ = true
				this.searchFlag = false
				this.GFlag = true
				this.titleFlag = true
				// this.searchData = ''
				this.getSeaDate = ''
			},
			// 存储搜索历史
			getSearchHistory(searchkey){
				// 存之前先取
				let searchArray = uni.getStorageSync('srach_key') || []
				searchArray.unshift(searchkey)
				uni.setStorageSync('srach_key',searchArray)
			},
			// 取出搜索历史
			setStorages(){
				let setdata = uni.getStorageSync('srach_key')
				// 数组去重
				let setdataarr = Array.from(new Set(setdata))
				// console.log(setdataarr);
				if(setdataarr.length === 0){
					this.ifhistory = false
				}else{
					this.seaDate = setdataarr
					this.ifhistory = true 
				}
				// console.log(this.seaDate);
			},
			// 搜索历史的搜索
			menubtn(item){
				this.ifhistory = false
				// this.pageid = 0
				this.searchData = item
				this.searchDate()
				// this.searchData(item)
			},
			
			
			// 返回
			fanHuiGCenter(){
				this.videoContext1.pause();
				this.$emit('fanHuiGameCenter')
			},
			touchstart(){
				// console.log('点击了暂停');
				this.videoContext1.pause();
			},
			videoPause(){
				this.videoFlag= false
			},
			clickImage(){
				this.videoContext1.play();
				this.videoFlag= true
			},
			goSearch(){
				this.videoContext1.pause();
				this.GFlag = false
				this.searchFlag = true
				this.AdventureFlag = false
				this.titleFlag = false
			},
			searchBack(){
				this.videoContext1.pause();
				this.GFlag = true
				this.searchFlag = false
				this.titleFlag = true
			},
			detailsBack(){
				this.videoContext1.pause();
				this.GFlag = true
				this.searchFlag = false
				this.AdventureFlag = false
			},
			slidingBack(){
				this.videoContext1.pause();
				this.GFlag = true
				this.AdventureFlag = false
				this.CompetitionFlag = false
				this.JourneyFlag = false
				this.SongDanceFlag = false
				
			},
			async goAdventure(id,url,name){
				this.videoContext1.pause();
				this.GFlag = false
				this.searchFlag = false
				this.AdventureFlag = true
				this.url = url
				this.name = name
				const res = await this.$myRequest({
					url:'/api/game/list'+'?categoryId='+ id +'&page=1'+'&limit=-1',
				}) 
				// console.log(res.data);
				// console.log(res.data.data);
				this.gameData = res.data.data

			},
		},

		
	}
</script>

<style scoped> 
	page{
		background-color: #EA5C2C;
	}
	.hours_174{
		background-color: #174a69;
	}
	.b-r-l-r-t46{
		border-top-left-radius: 46rpx;
		border-top-right-radius: 46rpx;
	}
	.b-r-l-r-t40{
		border-top-left-radius: 40rpx;
		border-top-right-radius: 40rpx;
	}
	.b-r-l-r-b30{
		border-bottom-left-radius: 30rpx;
		border-bottom-right-radius: 30rpx;
	}
	.top-left-right{
		border-top-right-radius: 30rpx;
		border-top-left-radius: 30rpx;
	}
	.box-games1{
		position: relative;
		height: 720rpx;
		z-index: 1;  
	}
	.games1{
		width: 290rpx; 
		height: 236rpx; 
		flex-direction: column;
	}
	
	.game3{
	}
	.game4{
		position: absolute; 
		/* top: -76rpx; */
		top: 0rpx;
		left: 0rpx; 
		width: 100%;
	}
	.br22rpx{
		border-radius: 22rpx;
	}
	.box-sha1{
		box-shadow: 0  2rpx 4rpx #bac6ce;
	}
</style>
