<template>
	<view class=""  style="position: relative;"> 
		<view class="" v-if="gamedaFlag">
			<view  class="FFF f36rpx ta-c fw800" style="margin-top: 0rpx;">{{titleName}} GAMES</view>
			<view class="" style="height: 130rpx;"></view>
			<!-- <view class="" style="height: 101rpx;"></view> -->
			<view @click="slidingBack"  class="" 
			hover-class="hover1" hover-start-time=0 hover-stay-time=150
			style="position: absolute; top: 3rpx; left: 60rpx;">
				<image src="../../static/Index/fanHui.png" mode="widthFix" style="width: 22rpx;"></image>
			</view> 
			<view class="top-left-right bgc_FFF" 
			style="margin: 0rpx 30rpx;  height: calc(100vh - 320rpx);">
				<view class="df gData1" :style='bgcH'>
					<view class="" style="padding: 0 10rpx 0 153rpx;">
						<image :src="src" mode="widthFix" style="width: 60rpx;"></image>
					</view>
					<view class="ta-c f27rpx FFF fw800" style="line-height: 111rpx;">{{title}}</view> 
				</view> 
				<!-- <view class="" style="height: 104rpx;"></view> -->
				
				<view class="">
					<view v-show="slidingFlag" class="" style="margin: 0 28rpx;">
						<scroll-view scroll-y="true" style="height: calc(100vh - 330rpx); width: 100%;" class="" >
							<view class="" style="height: 104rpx;"></view>
							<view class="" style="height: height: calc(100vh - 330rpx);">
								<view v-for="(item,index) in gameData.records" :key="item.id" @click="goGame(item)"
									style="margin: 0 0 22rpx 0; width: 300rpx; border-radius: 22rpx; border:5rpx solid #b0a1b6;" class="box">
									<view class="df-jcc-aic" style="height: 300rpx;"> 
										<image :src="item.iconUrl" mode="widthFix" style="width: 200rpx; "></image>
									</view>
									<view class="df-jcsb box-r-b sli_view" :style="bgcH"> 
										<view class="FFF ta-c f24rpx df-jcc-aic" style="width: 240rpx;">
											<view class="">
												{{item.title}}
											</view>
										</view>
										<view class="b-r-50 bgc_FFF df-jcc-aic" 
											style="width: 60rpx; height: 60rpx; margin-right: 5rpx; ">
											<image :src="smallImg" mode="widthFix" style="width: 24rpx;"></image>
										</view>
									</view>
								</view>
							</view>
							<view class="fahter" style="height: 60rpx;"></view>
							<!-- <view class="" style="height: 60rpx; position: absolute; bottom: 0rpx;">1</view> -->
						</scroll-view> 
					</view>

				</view>
				<!-- <slidingBlock @clackGame="clackGame" :bgc= 'bgcH' :smallImg="smallImg" :gameData="gameData"></slidingBlock> -->
			</view>
		</view>
		<view class="" v-show="gFlag">
			<view @click="backGameDetail"   class=""
				hover-class="hover1" hover-start-time=0 hover-stay-time=150
				style="position: absolute; left: 50rpx; top: 0rpx; z-index: 3;">
				<image src="../../static/Index/fanHui.png" mode="widthFix" style="width: 22rpx;"></image>
			</view> 
			<gamesDetail  :dates="dates" :day="days"></gamesDetail>
		</view>
	</view>
</template> 

<script>
	// import slidingBlock from './slidingBlock.vue'
	import gamesDetail from '../index/gameDetails.vue'
	export default {
		components:{
			gamesDetail
		},
		props:{
			title:String,
			bgcH:String,
			src:String,
			smallImg:String,
			gameData:Object,
			titleName:String
		},
		
		data(){
			return{
				gamedaFlag:true,
				slidingFlag:true,
				gFlag:false,
				day:'',
				days:'',
				dates:{},
				titleNameFlag:true
			}
		},
		methods:{
			slidingBack(){
				this.$emit('slidingBack')
			},
			openGame(){
				this.gamedaFlag = true
			},
			clackGame(){
				this.gamedaFlag = false
			},
			goGame(e){
				console.log(e);
				this.dates = e
				this.day = e.createTime
				console.log('day='+this.day);
				let year = this.day.slice(0,4)
				let month = this.day.slice(5,7)
				let day = this.day.slice(8,10)
				this.days = day + '/'+month+'/'+year
				this.gamedaFlag = false
				this.slidingFlag = false
				this.titleNameFlag = false
				this.gFlag = true
				// this.$emit("clackGame")
			},
			backGameDetail(){
				this.gFlag = false
				this.gamedaFlag = true
				this.slidingFlag = true
			}
		},
		onShow() {
			// console.log(this.name);
		}
	}
</script>

<style>
	page{background-color: #fdc84f;}
	.top-left-right{
		border-top-right-radius: 30rpx;
		border-top-left-radius: 30rpx;
	}
	.fahter{
	    overflow: hidden;
	}
	.gData1{
		align-items: center;
		border-radius: 22rpx; 
		width: 603rpx; 
		height: 111rpx;
		position: absolute;
		left: 74rpx;
		top: 115rpx;
		z-index: 1;	
	}
	.box-r-b{
		border-bottom-right-radius: 22rpx;
		border-bottom-left-radius: 22rpx;
	}
	.box:nth-child(2n+1){
		float: left;
	}
	.box:nth-child(2n){
		float: right;
	}
	.sli_view{
		height: 65rpx;  
		align-items: center;
	}
</style>
