<template>
	<view class="" style="height: 100vh;" v-show="allFlag" :class="windowHeight ? 'stt' : ''">
		<view class="bgc-o-y" style="position: fixed; bottom: 0; z-index: -30; height: 100vh; width: 750rpx;"></view>
		<popUp ref="games"></popUp>
		<TekyGoTop></TekyGoTop>
		<view class="" style="height: 40rpx;"></view> 
		<view class="" style=""> 
			<!-- 两点击栏 -->
			<view class="" v-show="cut">
				<view  class="FFF f36rpx ta-c fw800">CHOOSE HARDWARE</view> 
				<view class="" style="height: 70rpx;"></view>
				<view @click="cutTrampoline" v-if="Tname"
					class="df b-r-15 Tra_Squ bgc_L2c9" style="margin: 0 68rpx;"
					hover-class="hover" hover-start-time=0 hover-stay-time=150 >
					<view class="df-jcc-aic" style="padding-left: 24rpx;">
						<image class="b-r-50" src="../../static/img/Trampoline.png" mode="widthFix" 
							style="width: 120rpx;"></image>
					</view>
					<view class="ta-c FFF fw800 f45rpx Tra_text" style="">{{Tname}}</view> 
				</view>
				<view v-if="ifSque" @click="cutSqueezies" class="df b-r-15 Tra_Squ bgc_Za05" style="margin: 45rpx 68rpx 0;"
				hover-class="hover" hover-start-time=0 hover-stay-time=150 >
					<view class="df-jcc-aic" style="padding-left: 24rpx;">
						<image class="b-r-50" src="../../static/img/squeeze.png" mode="widthFix" 
						style="width: 120rpx;"></image>
					</view>
					<view class="ta-c FFF fw800 f45rpx Tra_text" style="">{{Sname}}</view> 
				</view> 
			</view>
			<!-- 蓝色 --> 
			<view class="" v-if="cutChoose" style="">
				<Games @fanHuiGameCenter="fanHuiGameCenter" ifx="ifL" 
				:bgc="bgcL" :box_sha="box_sha_L" :ifReday="ifReday" :TramListData="TramListData"
				:title="Tname" :img="image1" :TramData="TramData" :TramList="TramList" ></Games>
			</view> 
			</view>
			<!-- 紫色 -->
			<view class="" v-if="cutSqueez" style="">
				<Games @fanHuiGameCenter="fanHuiGameCenter" ifx="ifZ" 
				:bgc="bgcZ" :box_sha="box_sha_Z" :ifReday="ifReday" :TramListData="SqueListData"
				:title="Sname" :img="image2" :TramData="SqueData" :TramList="SqueList" ></Games>
			</view>
		</view>
	</view> 
	
</template> 
 
<script>
	import Games from './games.vue'
	export default {
		components:{Games}, 
		data() {
			return {
				cut:true,
				cutChoose:false,
				cutSqueez:false,
				Flag:true,
				allFlag:true,
				ifSque:false,
				image1:"../../static/img/Trampoline.png",
				image2:"../../static/img/squeeze.png",
				bgcL:"background-color: #2b94d0;",
				bgcZ:"background-color: #a0599f;",
				box_sha_L:"border: 5rpx solid #41bae7;box-shadow: 0rpx 2rpx  8rpx  #829986;", 
				box_sha_Z:"border: 5rpx solid #d38bd2;box-shadow: 0rpx 2rpx  8rpx  #8d557d;",
				Tram_id:'',
				Sque_id:'',
				TramData:{},
				SqueData:{},
				TramListData:{},
				SqueListData:{},
				TramList:[],
				SqueList:[],
				ifReday:'',
				Tname:'',
				Sname:'',
				windowHeight:false
			}
		},
		methods: {
			opendataAll(){
				this.allFlag = false
			},
			cleakdataAll(){
				this.allFlag = true
			},
			
			//获取游戏目录
			async getList(){
				let _this = this
				const res = await this.$myRequest({
					url:'/api/category/list',
				})  
				this.TramList = res.data.data.records[0].childList
				this.SqueList = res.data.data.records[1].childList
				this.TramListData = res.data.data.records[0]
				this.SqueListData = res.data.data.records[1]
				this.ifReday = res.data.data.searchCount
				this.Tram_id = res.data.data.records[0].id
				this.Sque_id = res.data.data.records[1].id
				this.Tname = res.data.data.records[0].name.toUpperCase()
				this.Sname = res.data.data.records[1].name.toUpperCase()
				//获取蹦床游戏列表
				const res1 = await this.$myRequest({ 
					url:'/api/game/list'+'?categoryId='+_this.Tram_id+'&page=1'+'&limit=-1',
				}) 
				if(res1.data.succeeded){
					this.TramData = res1.data
				}
				//获取健身球游戏列表
				const res2 = await this.$myRequest({ 
					url:'/api/game/list'+'?categoryId='+_this.Sque_id+'&page=1'+'&limit=-1',
				}) 
				if(res2.data.succeeded){
					if(res2.data.data.records == 0){
						_this.ifSque = false
					}else{
						_this.ifSque = true
						_this.SqueData = res2.data
					}
				}
				
				

			},
			
			cutTrampoline(){
				this.cut = false
				this.cutChoose = true
				this.Flag = true
			},
			cutSqueezies(){
				this.cut = false
				this.cutSqueez = true
				this.Flag = false
			},
			fanHuiGameCenter(){
				this.cut = true
				this.cutChoose = false
				this.cutSqueez = false
			}
			
		},
		onLoad() {
			// this.getList()
			uni.showLoading({
				title:'Loading...'
			})
			let time2 = setTimeout(function() {
				uni.hideLoading()
				console.log('1');
				// clearTimeout(time)   
			}, 1000); 
		},
		created() {
			this.getList()
			let that = this
			uni.getSystemInfo({
				success(res) {
					// console.log(res.windowHeight);
					if(res.windowHeight >700){
						that.windowHeight = true
					}else{
						that.windowHeight = false
					}
					// console.log(res.windowHeight);
				}
			})
		},
		onHide() {
			this.cut = true
			this.cutSqueez = false
			this.cutChoose = false
		},
		onShow() {
			// console.log('获取数据');
			this.getList()
			
			let _this = this
			uni.getNetworkType({
					success: function (res) {
							console.log(res.networkType);
							if(res.networkType == 'none'){
								console.log('无网络');
								_this.flags = 2
							}else{
								console.log('有网络');
								_this.flags = 1
							}
					}
			});
			this.$nextTick(function(){
				if(this.flags == 2){
					this.$refs.games.openPopup('Request out of time, please check network and try it again')
				}else{
					
				}
			})
		}
		
		
	}
</script>

<style>
	/* page{background-color: #fdc84f;} */
	.Tra_Squ{
		z-index: 1;  
		
		height: 160rpx;
	}
	.Tra_text{
		letter-spacing:3rpx; 
		height: 100%; 
		line-height: 160rpx;
		padding-left: 42rpx;
	}
	.xxx{
		    width: 100%;
		    height:280rpx;
		    border-bottom-left-radius:250rpx 100rpx;
		    border-bottom-right-radius:250rpx 100rpx;
	}
	
	
</style>
